.main-block {
  background: conic-gradient(from 130.31deg at 16.03% 39.06%, #B80027 -46.69deg, #003D85 32.24deg, #B80027 313.31deg, #003D85 392.24deg);

  min-width: 320px;
}

.main-block__wrapper {
  padding: 51px 32px 51px 51px;
  max-width: 1224px;
  display: grid;
  grid-template-columns: 377px auto;
  column-gap: 64px;

  @include vp-1199 {
    grid-template-columns: 324px auto;
    padding: 82px 45px;
    max-width: 1109;
    column-gap: 30px;
  }
}

.main-block__content-wrapper {
  margin-top: auto;
  margin-bottom: 43px;
  position: relative;
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: repeat(3, min-content);
  color: $color-default-white;
}

.main-block__city {
  margin: 0 50px 14px 0;
  font-family: $ff-alumni;
  font-style: normal;
  font-weight: 600;
  font-size: 48px;
  line-height: 58px;
  text-align: right;
  letter-spacing: 0.03em;
  grid-column: 2 / -1;
  align-self: end;

  @include vp-1199 {
    margin: 0;
    font-size: 34px;
    line-height: 41px;
  }
}

.main-block__title {
  margin: 0 0 50px;
  font-family: $ff-alumni;
  font-style: normal;
  font-weight: 600;
  font-size: 92px;
  line-height: 110px;
  text-align: right;
  letter-spacing: 0.05em;
  grid-column: 2 / -1;
  overflow: scroll;


  @include vp-1199 {
    font-size: 72px;
    line-height: 86px;
  }

}

.main-block__workout-list {
  margin: 125px 0 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  grid-row: 2 / 3;
}

.main-block__workout-item {
  font-family: $ff-roboto;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-align: right;
  min-width: 113px;
}

.main-block__button {
  margin-left: auto;
  grid-column: 2 /-1;
}

.main-block__image {
  max-width: 100%;
  // height: 100%;
  // width: 500px;
  // height: 556px;


  @include vp-1199 {
    min-width: 324px;
    min-height: 390px;
  }

}
