.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;

  min-width: 320px;
}

.header__wrapper {
  padding: 30px 32px 0;
  display: flex;
  justify-content: space-between;
  max-width: 1224px;

  @include vp-1199 {
    padding: 40px 45px 25px;
  }
}

.header__logo {
  margin-right: 80px;
}
